@import url(https://fonts.googleapis.com/css?family=Gothic+A1&display=swap);
@import url(https://fonts.googleapis.com/css?family=Gothic+A1&display=swap);
#company-page .candidate .company h2 {
  font-family: 'Gothic A1', sans-serif; }

#company-page .candidate .company:after {
  content: "";
  display: table;
  clear: both; }

#company-page .candidate > h3 {
  margin: 20px 0 0 0;
  padding: 0 0 0 10px;
  font-size: 1rem; }

#company-page .candidate > ul {
  margin: 0;
  padding: 0;
  list-style: none; }

#company-page .candidate .company {
  display: block;
  padding: 10px 20px 20px 10px; }
  #company-page .candidate .company .thumb {
    float: left;
    margin: 0 10px 0 0;
    background: pink;
    height: 100%; }
    #company-page .candidate .company .thumb img {
      width: 44px;
      height: 44px;
      border-radius: 4px;
      display: block; }
  #company-page .candidate .company h2 {
    margin: 0;
    padding: 0;
    font-size: 0.9rem;
    font-weight: 500;
    color: #212529; }
    #company-page .candidate .company h2 small {
      margin-left: 5px;
      color: #495057; }
  #company-page .candidate .company .description {
    margin-top: 5px;
    color: #495057;
    font-size: .8rem;
    line-height: .8rem;
    max-height: .8rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }

body {
  font-family: "Noto Sans KR", sans-serif, AppleSDGothicNeo-Regular, "Malgun Gothic", Dotum; }

#header nav a {
  font-family: 'Gothic A1', sans-serif; }

#header header:after, #header nav:after {
  content: "";
  display: table;
  clear: both; }

html, body {
  margin: 0;
  padding: 0;
  font-size: 14px; }

a {
  text-decoration: none; }

#header header {
  position: relative; }
  #header header a.logo {
    float: left;
    display: block;
    padding: 18px 10px; }
    #header header a.logo img {
      height: 14px;
      display: block; }
  #header header ul.anchors {
    list-style: none;
    margin: 0;
    padding: 0;
    float: right; }
    #header header ul.anchors li {
      float: left; }
      #header header ul.anchors li a {
        display: block;
        padding: 13px 6.5px; }
        #header header ul.anchors li a img {
          display: block;
          width: 24px;
          height: 24px;
          border-radius: 5px; }
      #header header ul.anchors li:last-child a {
        padding-right: 13px; }

#header nav {
  background: #343a40;
  position: relative;
  padding-left: 48px;
  overflow: hidden; }
  #header nav a {
    color: #868e96;
    font-size: 1.1rem; }
  #header nav .convert {
    position: absolute;
    left: 0;
    top: 0;
    display: block;
    float: left;
    width: 45px;
    height: 45px;
    line-height: 45px;
    text-align: center; }
    #header nav .convert > div {
      position: relative; }
      #header nav .convert > div::after {
        display: block;
        content: ' ';
        position: absolute;
        width: 1px;
        height: 50%;
        top: 25%;
        right: -3px;
        background: #495057; }
  #header nav ul.category {
    margin: 0;
    padding: 0;
    max-width: 100%;
    max-height: 45px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap; }
    #header nav ul.category::-webkit-scrollbar {
      display: none;
      -webkit-overflow-scrolling: touch; }
    #header nav ul.category li {
      display: inline-block; }
      #header nav ul.category li.c0 {
        display: none; }
      #header nav ul.category li a {
        display: block;
        width: 45px;
        height: 45px;
        text-align: center;
        line-height: 45px; }
        #header nav ul.category li a.active {
          color: #ffffff;
          font-weight: 700; }
  #header nav.ko .convert .ko {
    color: #ffffff; }
  #header nav.en .convert .en {
    color: #ffffff; }

